import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { UtilsProvider } from '../../providers/utils/utils';
import { HttpProvider } from '../../providers/http/http';
import { CommandsProvider } from '../../providers/commands/commands';

/**
 * Generated class for the ConfirmPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-confirm',
  templateUrl: 'confirm.html',
})
export class ConfirmPage {

  public payee: string;
  public account: string;
  public valor: number;
  public flag: string;
  public cardNumber: string;
  public valorSelected: string;
  public accountToDebit: string;
  public message: string;
  public pos: string;
  public posAccount: string;
  public comprobantePayBill: string;
  public comprobanteTransfer: string;
  public comprobanteCreditCard: string;
  public accountTo: string;
  public email: string;
  private PAY_BILL:string ='payBill';
  private CREDIT_CARD:string ='creditCard';
  private TRANSFER:string ='transfer';
  public tipoPagoTarjeCredit: string;
  public nameCreditCard: string;
  public misCuentas: string;
  public accountInfo: string;

  //Valores de los nombre en el confirm
  public item1: string;
  public item2: string;
  public item3: string;
  public item4: string;
  //public mostrarCorreo: string = document.getElementById('oculto').style.visibility='visible';
  public verEmail: boolean = false;


  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, private alertCtrl: AlertController, public utils: UtilsProvider,
               public http: HttpProvider, public command: CommandsProvider) {
    
    //obtención de la bandera para poder ingresar a cualquier pantalla
    this.flag =  this.navParams.get("flag");

    if(this.flag == this.TRANSFER){

        //variable para mostrar o no el e-mail
        this.verEmail = true;

        //Valores de los nombres del confirm dependiendo de la pagina - Esta es para la PAGINA TRANSFER              
        this.item1 = this.utils.getMessageLanguage("transferProduct.accountTo");
        this.item2 = this.utils.getMessageLanguage("transferProduct.accountFrom");
        this.item3 = this.utils.getMessageLanguage("Filtro.amount");
        this.item4 = this.utils.getMessageLanguage("addAccount.email");

        //Parametros para la transferecia desde transferProduct
        this.payee = this.navParams.get("payee");
        this.account = this.navParams.get("account");  
        this.valor = this.navParams.get("valor"); 
        if(this.navParams.get("email") != null) { this.email = this.navParams.get("email"); }

        //ESTO ES PARA EL PAGO DE TRANSFERENCIA
        //se obtiene la cuenta completa selecciona para el confirm de la transferencia
        this.storage.get('accountTo').then((val) => { if (val != null) { this.accountTo = val; } });

    }


    //Obtención del tipo de pago -> Pagina amount_to_pay for CreditCardPage
    if(this.flag==this.CREDIT_CARD){

        //obtencion de los nombre de los items que van en el confirm
        this.item1 = this.utils.getMessageLanguage("productToPay.cardNumber");
        this.item2 = this.utils.getMessageLanguage("productToPay.valorPago");
        this.item3 = this.utils.getMessageLanguage("productToPay.cuenta");

        if(this.navParams.get("cardNumber") != null){ this.payee = this.navParams.get("cardNumber"); }
    
        if(this.navParams.get("valorSelected") != null){ this.valor = this.navParams.get("valorSelected"); }
    
        if(this.navParams.get("accountToDebit") != null){ this.account = this.navParams.get("accountToDebit"); }

        this.storage.get('cuot').then((val) => { if (val != null) { this.tipoPagoTarjeCredit = val; } });

        this.storage.get('tota').then((val) => { if (val != null) { this.tipoPagoTarjeCredit = val; } });

        this.storage.get('otro').then((val) => { if (val != null) { this.tipoPagoTarjeCredit = val; } });

        //Obtencion del nombre de la tarjeta de p-credit 
        this.storage.get('name_CreditCard').then((val) => { if (val != null) { this.nameCreditCard = val; }
        });
    }//end flag CREDIT_CARD



    if(this.flag==this.PAY_BILL){

        //obtencion de los nombre de los items que van en el confirm
        this.item1 = this.utils.getMessageLanguage("productToPayBill.producto");
        this.item2 = this.utils.getMessageLanguage("productToPayBill.cuenta");
        this.item3 = this.utils.getMessageLanguage("Filtro.amount");
        
        this.accountInfo = this.navParams.get("accountInfo");

        this.payee = this.navParams.get("payee");
        this.account = this.navParams.get("account");  
        this.valor = this.navParams.get("valor"); 

        //TODO ESTO ES PARA EL PAGO DE PAY BILLS
        //se obtiene todo el elemento seleccionado de la lista de elementos de paybills -> ESTA NO SE ESTA UTILIZANDO por el momento
        this.storage.get('posElementoPayBills').then((val) => { if (val != null) { this.pos = val; } });

        //se obtiene la Posición del elemento seleccionado de la lista de elementos de FACTURAS paybills ->pantalla bills to pay
        this.storage.get('posAccount').then((val) => { if (val != null) { this.posAccount = val; } });

        //se obtiene mis cuentas que vienen de la pantalla bills to pay
        this.storage.get('misCuentas').then((val) => { if (val != null) { this.misCuentas = val; } });


    }//END flag PAY_BILL
    

  }

  //Metodo de confirmación de pago
  confirmar(){
    
    switch (this.flag) {
      case this.TRANSFER:
        this.presentConfirm("TransferPage");

      break;
      case this.CREDIT_CARD:
      
           if(this.navParams.get("cardNumber") != null){
              this.presentConfirm("RutaPagosPage");
            }
            else{
              this.presentConfirm("RutaPagosPage");
            }
      break;
      case this.PAY_BILL:
              this.presentConfirm("RutaPagosPage"); 
      break;
      default:
      break;

    }


  }

  //Metodo de confirmación
  presentConfirm(url) {
    let alert = this.alertCtrl.create({
      title: this.utils.getMessageLanguage("confirm.title"),
      message: this.utils.getMessageLanguage("confirm.msj"),
      buttons: [
        {
          text: this.utils.getMessageLanguage("confirm.btnAceptar"),
          handler: data => {
            if (this.utils.getMessageLanguage("confirm.btnAceptar")) 
            {              
              
              switch (this.flag) {
                case this.TRANSFER:
                    this.transfer();
                  break;
                  case this.CREDIT_CARD:
                      this.pagoCreditCard();
                  break;
                  case this.PAY_BILL:  
                    this.pagoPayBills();
                  break;
                default:
                  break;
              }
              console.log("acepto");
              
            }
          } 
        },
        {
          text: this.utils.getMessageLanguage("confirm.btnCancelar"),
          handler: data => {
            if (this.utils.getMessageLanguage("confirm.btnCancelar")) 
            {
              this.storage.remove('numero');
              this.storage.remove('accountSelectedTopay');
              this.storage.remove('valorSeleccionado');
              this.storage.remove('cuentaTransferSelected');
              this.navCtrl.push(url);
            }
          }
        }
      ]
    });
    alert.present();
  }

  //Metodo para hacer el pago de pay bills
  private pagoPayBills(){

    var command :string[] =  this.utils.getCommands(this.command.E2LSMR6081);
    //var pro = "E2LSMR6081";
    //var cmd = "E2LSMR6081";
    var ht_local = "";
    var ht_rrn = "0";
    var CA_PAY_0 = this.valor;
    var CA_MEMO_0 = " ";
    //var CA_CUENTAD_0= this.payee;

   // var URL=  cmd+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW='+ht_local+' ºPayº º'+pro+' º'+ht_rrn+' º-1 º&PAR='+"º"+pos+"º"+ CA_CUENTAD_0 +"º CA_PAY_0 +º "+ CA_MEMO_0+" º";
    var URL=  command[0]+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW='+ht_local+ 
    this.utils.separator +'Pay'+ this.utils.separator + this.utils.separator +command[0]+ this.utils.separator + ht_rrn+ this.utils.separator +'-1 '+ this.utils.separator +
    '&PAR='+ this.posAccount+this.utils.separator+this.accountInfo+ this.utils.separator + CA_PAY_0 + this.utils.separator + CA_MEMO_0+ this.utils.separator;

    this.http.callServer(URL).then(res => {      
      this.message = res.message;
  
        if (this.message != '' && typeof this.message!=="undefined") {
          this.utils.alertCampos(this.message);
        } else {
          this.comprobantePayBill = res.lista1;
          //this.storage.set('comprobantePayBill', this.comprobantePayBill );
          this.storage.remove('numero');
          this.storage.remove('accountSelectedTopay');
          this.storage.remove('valorSeleccionado');
          this.storage.remove('cuentaTransferSelected');
          this.navCtrl.push("VoucherPayBillPage", {comprobantePayBill: this.comprobantePayBill});
        }
      },
        error => {
          console.log(error);
        });
  }

  //Metodo para hacer transferencias
  private transfer(){
    
    var command :string[] =  this.utils.getCommands(this.command.E2ADMR43);
    //var cmd = "E2ADMR43";
    var acountFrom = this.account;
    var accountTo = this.accountTo;
    var monto = this.valor;
    var memo = " ";
    //var email = this.email;
    var email = "";
    if(this.email != undefined){
      email = this.email;
    }
    else {
      email = "";
    }
    //alert("valor acountFrom "+ acountFrom + "\nvalor accountTo "+accountTo + "\nmonto "+monto);
    
    var URL=  command[0]+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW='+
    this.utils.separator+"Enviar" + this.utils.separator+this.utils.separator+command[0]+this.utils.separator+'0'+this.utils.separator+this.utils.separator+
    '&PAR='+ acountFrom+this.utils.separator+accountTo+ this.utils.separator + monto + this.utils.separator+ memo + this.utils.separator + email;

    this.http.callServer(URL).then(res => {      
      this.message = res.message;
  
        if (this.message != '' && typeof this.message!=="undefined") {
          this.utils.alertCampos(this.message);
        } else {
          this.comprobanteTransfer = res.lista1;
          //this.storage.set('comprobantePayBill', this.comprobantePayBill );
          this.storage.remove('numero');
          this.storage.remove('accountSelectedTopay');
          this.storage.remove('valorSeleccionado');
          this.storage.remove('cuentaTransferSelected');
          this.navCtrl.push("VoucherTransferPage", {comprobanteTransfer: this.comprobanteTransfer});
        }
      },
        error => {
          console.log(error);
        });
  }

  //Metodo pago de credit Card
  private pagoCreditCard(){

    var command :string[] =  this.utils.getCommands(this.command.E2ADMR67PT);
    //var cmd = "E2ADMR67PT";
    var tipoPago = this.tipoPagoTarjeCredit;
    var account = this.account;
    var monto: number = this.valor;
    //alert("Tipo de pago: "+tipoPago+"\n cuenta a descontar: "+ account+"\n Monto: "+ monto);    
    var URL=  command[0]+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW='+
    this.utils.separator+"Enviar" + this.utils.separator+this.utils.separator + 'E2LSCR89L' +this.utils.separator+'0'+this.utils.separator+
    tipoPago + this.utils.separator + '&PAR='+tipoPago + this.utils.separator + monto + this.utils.separator + account + this.utils.separator +
    this.utils.separator + this.utils.separator+this.nameCreditCard + this.utils.separator;

    this.http.callServer(URL).then(res => {      
      this.message = res.message;
  
        if (this.message != '' && typeof this.message!=="undefined") {
          this.utils.alertCampos(this.message);
        } else {
          
          this.comprobanteCreditCard = res.lista1;
          
          this.navCtrl.push("VoucherCreditCardPage", {comprobanteCreditCard: this.comprobanteCreditCard});
          
        }
      },
        error => {
          console.log(error);
        });
  }

  alertCampos(param) {
    var alertFecha = param;    
    let alert = this.alertCtrl.create({
      title: alertFecha,
      buttons: ['OK']
    });
    alert.present();
  }

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }

  cancel(){
    
    switch (this.flag) {
      
      case this.TRANSFER:
        this.navCtrl.push("TransferPage", { valided: true });
      break;

      case this.CREDIT_CARD:
        this.navCtrl.push("RutaPagosPage");;
      break;

      case this.PAY_BILL:
        this.navCtrl.push("RutaPagosPage");
      break;

      default:
      break;
    }

  }
  

  public voucher(){
    this.navCtrl.push("VoucherCreditCardPage");
  }

}



